function withoutThrow(func) {
  return async (...params) => {
    try {
      return await func(...params)
    } catch (error) {
      // debugger
      notification['error']({
        style: {
          backgroundColor: 'white'
        },
        message: String(error),
      })
      return null
    }
  }
}

