let relayApi = axios.create({
  // timeout: 10000,
  baseURL: serverUrl,
})

relayApi.interceptors.request.use(function (config) {
  let rootState = store.getState()
  let { dexAccount: { apiKey } } = rootState
  let needApiKey = config => {
    let { url, method } = config
    if (url === '/apiKey' && method === 'get') {
      return false
    }
    return true
  }
  if (needApiKey(config)) {
    config.headers.common['X-API-KEY'] = apiKey
  }
  return config
})

relayApi.interceptors.response.use(function (response) {
  const data = response['data'];
  const resultInfo = data['resultInfo'];

  // Some API endpoints in the old versions don't have resultInfo.
  if (resultInfo) {
    const { code, message } = resultInfo;
    if (code !== 0) {
      // 
      if (message === 'ERR_REST_INVALID_SIGNATURE') {
        dispatch.appState.update({
          notification: {
            type: 'error',
            message: 'notify.socket.subscribe.error.apikey'
          },
        })
        dispatch.dexAccount.logout()
      }

      // TODO: 
      let errorCodeMapping = {
        104208: ''
      }
      let errorMsgMapping = {
        'nounce too low': ''
      }
      let errorMessage = errorCodeMapping[code] || errorMsgMapping[message] || `${code} ${message}`
      notification['error']({ message: errorMessage })
    }
  }
  
  return data
}, function (error) {
  if (error.message === "Network Error") {
    notification['error']({ message: '' })
  }

  if (error.response) {
    notification['error']({ message: error.response.data })
  } else {
    notification['error']({ message: '' })
  }
  return Promise.reject(error)
})

async function getTokenBalance(owner, tokenAddress) {
  const params = {
    owner,
    token: tokenAddress
  };

  const response = await relayApi.get('/tokenBalances', {params});
  return response['data'];
}

