async function getApiKey(params, signed) {
  const signature = signed.Rx + ',' + signed.Ry + ',' + signed.s;
  const headers = {
    'X-API-SIG': signature
  };
  const response = await relayApi.get('/apiKey', {
    params,
    headers
  })
  return response.data
}

async function getTickers(params) {
  let market = params.markets.join(',')
  let resp = await relayApi.get('/ticker', {params: {
    market
  }})

  let tickers = resp.data.map(ticker => {
    let [ market, timestamp, size, volume, open, high, low, close, count, bid, ask ] = ticker
    return { market, timestamp, size, volume, open, high, low, close, count, bid, ask }
  })
  return tickers
}

async function getPrice(params) {
  const response = await relayApi.get('/price', {params});
  return response['data'];
}

async function getTransactions(params) {
  let { accountId, offset, limit, statuses, types, start, end, symbol: tokenSymbol } = params
  
  // v1v2 
  let type = types && types[0] || 'deposit'
  const response = await relayApi.get(`/user/${type === 'deposit' ? 'deposits' : 'withdrawals'}`, {
    params: {
      accountId,
      // 
      allType: true,
      offset,
      limit,
      status: statuses && statuses.join(','),
      start,
      end,
      tokenSymbol
    }
  })

  const responseData = response['data'];
  let txs = responseData['transactions'];
  return txs.map(tx => {
    tx.txType = type
    tx.realAmount = tx.amount
    return tx
  })
}

