async function getRecommendedGasPrice() {
  const response = await relayApi.get('/recommendedGasPrice')
  const price = response['data'];
  let minPrice = BigNumber(price)
  let normalPrice = minPrice.plus(2 * 1e9)
  let maxPrice = minPrice.times(30)
  return [minPrice, normalPrice, maxPrice]

}

async function getAllowance(owner, symbol) {
  let token = config.getTokenBySymbol(symbol);
  let tokenAddress = token.address;
  const params = {
    owner,
    token: tokenAddress
  };

  const response = await relayApi.get('/allowances', {params})
  return response['data']
}

