import _ from 'lodash'
import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import numeral from 'numeral'
import moment from 'moment'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text,
} from 'rebass'
import { ENUM_LANGUAGE_TYPE } from 'utils/enum'
import { renderIf } from 'utils';

@injectIntl
class TokenInfo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
  }

  render() {
    let { tokenInfo, persistData: { locale } } = this.props
    if (!tokenInfo.id) return <></>

    let lang = ENUM_LANGUAGE_TYPE[locale].getValue()
    let { name, fullname, marketValue, publishDate, rank, publishAmount, homepage, flowAmount, whitePaper, publishPrice, desc, leverToken = {} } = tokenInfo
    let descInLocale = desc && desc[lang] || ''
    let { name: leverTokenName, baseName: underlying, quoteName: currency, lever, purchased, currWorth, lastWorth, lastSwapTime } = leverToken

    return (
      <Box {...this.props} px={30} pt={30} color="white" variant='noScrollbar' bg="backgroundTint" overflowY='auto'>
        <Flex fontSize={16}>
          <Text>{name}</Text>
          <Text color="#78788C">{fullname}</Text>
        </Flex>
        {
          renderIf(!currWorth)(
            <Flex fontSize={12} justifyContent="space-between">
              <Flex pr={40} flexDirection="column">
                <Flex pt={20}>
                  <Text width={65} color="#78788C"><FormattedMessage id="tokenInfo.label.marketValue" /></Text>
                  <Text>${numeral(marketValue).format('0,0')}</Text>
                </Flex>
                <Flex pt={20}>
                  <Text width={65} color="#78788C"><FormattedMessage id="tokenInfo.label.rank" /></Text>
                  <Text>{rank}</Text>
                </Flex>
                <Flex pt={20}>
                  <Text width={65} color="#78788C"><FormattedMessage id="tokenInfo.label.homepage" /></Text>
                  <Text color="#0064FF" sx={{cursor: 'pointer'}} onClick={() => window.open(homepage, '_blank')}>{homepage}</Text>
                </Flex>
                <Flex pt={20}>
                  <Text width={65} color="#78788C"><FormattedMessage id="tokenInfo.label.whitePaper" /></Text>
                  <Text color="#0064FF" sx={{cursor: 'pointer'}} onClick={() => window.open(whitePaper, '_blank')}>{whitePaper}</Text>
                </Flex>
              </Flex>
              <Flex flexDirection="column">
                <Flex pt={20}>
                  <Text width={105} color="#78788C"><FormattedMessage id="tokenInfo.label.publishDate" /></Text>
                  <Text>{moment(publishDate).format('YYYY-MM-DD')}</Text>
                </Flex>
                <Flex pt={20}>
                  <Text width={105} color="#78788C"><FormattedMessage id="tokenInfo.label.publishAmount" /></Text>
                  <Text>{numeral(publishAmount).format('0,0')}</Text>
                </Flex>
                <Flex pt={20}>
                  <Text width={105} color="#78788C"><FormattedMessage id="tokenInfo.label.flowAmount" /></Text>
                  <Text>{numeral(flowAmount).format('0,0')}</Text>
                </Flex>
                <Flex pt={20}>
                  <Text width={105} color="#78788C"><FormattedMessage id="tokenInfo.label.publishPrice" /></Text>
                  <Text>{numeral(publishPrice).format('0,0')}</Text>
                </Flex>
              </Flex>
            </Flex>
          )
        }
        {
          renderIf(currWorth)(
            <Flex fontSize={12} justifyContent="space-between">
              <Flex pr={40} flexDirection="column">
                <Flex pt={20}>
                  <Text width={65} color="#78788C"><FormattedMessage id="tokenInfo.label.lever.underlying" /></Text>
                  <Text>{underlying}</Text>
                </Flex>
                <Flex pt={20}>
                  <Text width={65} color="#78788C"><FormattedMessage id="tokenInfo.label.lever.leverage" /></Text>
                  <Text>{Math.abs(lever)}</Text>
                </Flex>
                <Flex pt={20}>
                  <Text width={65} color="#78788C"><FormattedMessage id="tokenInfo.label.lever.purchased" /></Text>
                  <Text>{numeral(purchased).format('0,0')} {leverTokenName}</Text>
                </Flex>
                <Flex pt={20}>
                  <Text width={65} color="#78788C"><FormattedMessage id="tokenInfo.label.lever.currency" /></Text>
                  <Text>{currency}</Text>
                </Flex>
              </Flex>
              <Flex flexDirection="column">
                <Flex pt={20}>
                  <Text width={105} color="#78788C">{underlying}<FormattedMessage id="tokenInfo.label.lever.underlyTrend" /></Text>
                  <Text color="#0064FF" sx={{cursor: 'pointer'}} onClick={() => window.open('http://bee.network', '_blank')}>http://bee.network</Text>
                </Flex>
                <Flex pt={20}>
                  <Text width={105} color="#78788C"><FormattedMessage id="tokenInfo.label.lever.currWorth" /></Text>
                  <Text>{numeral(currWorth).format('0,0.[00]')} {currency}</Text>
                </Flex>
                <Flex pt={20}>
                  <Text width={105} color="#78788C"><FormattedMessage id="tokenInfo.label.lever.lastSwapTime" /></Text>
                  <Text>{moment(lastSwapTime).format('YYYY-MM-DD HH:mm:ss')}</Text>
                </Flex>
                <Flex pt={20}>
                  <Text width={105} color="#78788C"><FormattedMessage id="tokenInfo.label.lever.lastWorth" /></Text>
                  <Text>{numeral(lastWorth).format('0,0.[00]')} {currency}</Text>
                </Flex>
              </Flex>
            </Flex>
          )
        }
        <Flex alignItems='center' justifyContent='space-between' bg="background">
        </Flex>
        <Flex pt={20} fontSize={12} maxWidth={550} flexDirection="column">
          <Text flexBasis="10%" color="#78788C"><FormattedMessage id="tokenInfo.label.desc" /></Text>
          <Text my={20} height={100} lineHeight={'25px'} overflowY='auto' variant='noScrollbar'>{descInLocale}</Text>
        </Flex>
      </Box>
    )
  }
}

const mapStateToProps = ({persistData}) => {
  return { persistData }
};

const mapDispatchToProps = ({markets}) => {
  return {
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(TokenInfo));