import React from 'react';
import _ from 'lodash';
import { connect } from 'react-redux';
import { withRouter } from "react-router-dom"
import { injectIntl, FormattedMessage  } from 'react-intl'
import {
  Flex,
  Box,
  Card,
  Image,
  Heading,
  Text
} from 'rebass'
import { Menu, Dropdown, Carousel } from 'antd';
import { BillPreCheck } from './Common'
import utils, { renderIf } from 'utils';
import metamaskLogo from 'metamask.png';
import wedexLogo from 'icons/wedexLogo.svg';
import { ENUM_ACCOUNT_STATUS } from 'utils/enum'
import { UpsertPasswordModal } from 'widgets/Dialog'
import api from 'api'

@injectIntl
class TopNav extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      activitis: [
        { title: 'WEDEX926', link: 'http://www.baidu.com' },
        { title: '10', link: 'http://www.baidu.com' },
        { title: 'WEDEX926', link: 'http://www.baidu.com' },
      ],
      notices: []
    }
  }

  componentDidMount() {
    this.fetchNotice()
  }

  async fetchNotice () {
    let resp = await api.getNotice({
      enable: true,
      page: 1,
      pageSize: 10
    })
    this.setState({ notices: resp.data.rows })
  }

  login() {
    this.billPreCheck.check()
  }

  async changePassword () {
    this.billPreCheck.checkSignedIn()
    await UpsertPasswordModal()
  }
  
  async clearStore () {
    this.props.logout()
  }

  renderNav () {
    const { persistData: { locale }, intl, updatePersistData } = this.props
    const menus = {
      mine: [
        { title: intl.formatMessage({id: 'navBar.mine.balance'}), link: '/mine/balance' },
        { title: intl.formatMessage({id: 'mine.nav.deposit'}), link: '/mine/deposit' },
        { title: intl.formatMessage({id: 'mine.nav.withdraw'}), link: '/mine/withdraw' },
        { title: intl.formatMessage({id: 'navBar.mine.order'}), link: '/mine/order' },
        { title: intl.formatMessage({id: 'navBar.mine.apikey'}), link: '/mine/apikey' },
        { title: intl.formatMessage({id: 'navBar.mine.award'}), link: '/mine/award' },
        { title: intl.formatMessage({id: 'navBar.mine.resetPwd'}), callback: () => this.changePassword() },
        { title: intl.formatMessage({id: 'navBar.mine.clean'}), callback: () => this.clearStore() },
      ],
      activity: [
        { title: intl.formatMessage({id: 'navBar.help.notice'}), newWindow: true, link: 'https://wedex.live/support/announcement' },
        { title: intl.formatMessage({id: 'navBar.help.tutorial'}), newWindow: true, link: 'https://wedex.live/support/guide' },
        { title: intl.formatMessage({id: 'navBar.help.faq'}), newWindow: true, link: 'https://wedex.live/support/faq' },
        // { title: '', newWindow: true, link: '' },
        { title: intl.formatMessage({id: 'navBar.help.about'}), newWindow: true, link: 'https://wedex.live/support/about' },
      ],
      language: [
        { title: ' ', callback: () => updatePersistData({locale: 'cn'}) },
        { title: ' English', callback: () => updatePersistData({locale: 'us'}) },
        { title: ' ', callback: () => updatePersistData({locale: 'kr'}) },
        { title: ' ', callback: () => updatePersistData({locale: 'ru'}) },
        { title: ' ', callback: () => updatePersistData({locale: 'jp'}) },
      ]
    }
    const genMenu = subMenus => (
      <Menu onClick={({key: index}) => {
        let menu = subMenus[index]
        if (menu.link) {
          // newWindow
          // TODO: 
          if (menu.newWindow) {
            window.open(menu.link, '_blank')
          } else {
            this.props.history.push(`/${locale}${menu.link}`)
          }
        } else if (menu.callback) {
          menu.callback()
        }
      }}>
      {
        subMenus.map((menu, index) => (
          <Menu.Item key={index}>
            <Text fontSize={[0, 0, 0]} color='hint'>{ menu.title }</Text>
          </Menu.Item>
        ))
      }
      </Menu>
    )

    let { address, state } = this.props.dexAccount
    let { isDesiredBrowser, isDesiredNetwork } = this.props.metaMask
    // enabledmetamaskadderss 
    let signined = utils.checkLogined()
    let accountCreating = ENUM_ACCOUNT_STATUS.CREATING.equals(state)

    return (
      <Flex alignItems='center'>
        {renderIf(isDesiredNetwork && signined && !accountCreating)(
          <Dropdown overlay={genMenu(menus.mine)} placement="bottomCenter" overlayClassName="nav-menu">
            <Flex px={25} height={40} alignItems='center'>
              <Image src={metamaskLogo} width={[25, 25, 25]} height={[25, 25, 25]} />
              <Box pl={3} fontSize={`13px`} fontWeight="bold">{`${_.take(address, 6).join('')}...${_.takeRight(address, 4).join('')}`}</Box>
            </Flex>
          </Dropdown>
        )}
        {renderIf(!isDesiredBrowser || !isDesiredNetwork || !signined || accountCreating)(
          <Flex px={25} height={40} alignItems='center' sx={{ cursor: 'pointer' }}
            onClick={() => this.login()}
          >
            {/* <Box pl={3} fontSize={`13px`} fontWeight="bold">{!isDesiredBrowser ? 'MetaMask' : !isDesiredNetwork ? '' : accountCreating ? '' : 'MetaMask'}</Box> */}
            <Box pl={3} fontSize={`13px`} fontWeight="bold">
              <FormattedMessage id={accountCreating ? 'navBar.signIn.creating' : 'navBar.signIn.login'}/></Box>
          </Flex>
        )}
        <Box height={40} lineHeight={'40px'} px={25} fontSize={`13px`} fontWeight="bold" sx={{cursor: 'pointer'}}
          onClick={() => window.open(`${window.location.origin}/${locale}/activities`, '_blank')}
        >
          <FormattedMessage id="navBar.activity" />
        </Box>
        <Dropdown overlay={genMenu(menus.activity)} placement="bottomCenter" overlayClassName="nav-menu" sx={{cursor: 'pointer'}}>
          <Box height={40} lineHeight={'40px'} px={25} fontSize={`13px`} fontWeight="bold"><FormattedMessage id="navBar.help"/></Box>
        </Dropdown>
        <Dropdown overlay={genMenu(menus.language)} placement="bottomCenter" overlayClassName="nav-menu" sx={{cursor: 'pointer'}}>
          <Box height={40} lineHeight={'40px'} px={25} fontSize={`13px`} fontWeight="bold"><FormattedMessage id="navBar.lang"/></Box>
        </Dropdown>
        <BillPreCheck ref={ref => this.billPreCheck = ref}></BillPreCheck>
      </Flex>
    )
  }

  render() {
    let { notices } = this.state
    return (
      <Box>
        <Flex variant='topNav' px={15}>
          <Flex width={120} alignItems='center' justifyContent='start' variant="clickable" onClick={() => {
            window.open(`http://www.wedex.live/`, '_blank')
          }}>
            <Image src={wedexLogo} width={150} />
            {/* <Text color="white" ml={2} fontSize={3}>WEDEX</Text> */}
          </Flex>
          <Box ml="50px" width={350} height={40}>
            {/* https://github.com/akiran/react-slick/issues/363 */}
            {/* https://easings.net/# */}
            <Carousel dots={false} draggable={false} autoplay={true} autoplaySpeed={8000} easing='cubic-bezier(0.68, -0.6, 0.32, 1.6)'>
              {
                _.map(notices, notice => {
                  let { color, link, text } = notice
                  return (
                    <Box height="40px" lineHeight="40px" fontSize={13} fontWeight={700}
                      color={`#${color}`}>
                      <Text className="text-ellipsis" sx={{
                        cursor: link ? 'pointer' : '',
                        width: "fit-content",
                        maxWidth: 350
                      }} onClick={() => {
                        if (!link) return
                        window.open(link, '_blank')
                      }}>{text}</Text>
                    </Box>
                  )
                })
              }
            </Carousel>
          </Box>
          <Flex alignItems='center' flex={1}>
            {/* {
              _.map(this.state.activitis, (activity, index) => (
                <Text key={index} mr={40} fontSize={[0, 0, 0]} color='hint'>{ activity.title }</Text>
              ))
            } */}
          </Flex>
          { this.renderNav() }
        </Flex>
        <Box as='hr' variant='divider.topNav'
          // sx={{
          // }}
        />
      </Box>
    )
  }
}

const mapStateToProps = ({ dexAccount, metaMask, persistData }) => {
  return {
    dexAccount,
    metaMask,
    persistData
  };
};

const mapDispatchToProps = ({ dexAccount, persistData }) => {
  return {
    logout: dexAccount.logout,
    updatePersistData: persistData.update
  };
};

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(TopNav))